export default {
	data() {
		return {
			
		}
	},
	onLoad() {

	},
	methods: {
		link(url,isLink,type) {
			if(!isLink){
				uni.navigateTo({
					url: `${url}?type=${type}`
				})
			}else{
				this.$store.dispatch('getAutoPower',{
					type: type
				}).then(res=>{
					uni.navigateTo({
						url: `${url}?type=${type}`
					})
				}).catch(err=>{})
			}
		},
	}
}
